(function() {
  angular.module('politicalGaps').service('cluster', function() {
    return function(alpha, nodes, NODE_RADIUS) {
      if (NODE_RADIUS == null) {
        NODE_RADIUS = 10;
      }
      return function(d) {
        var cluster, l, r, x, y;
        cluster = clusters[d.cluster];
        if (cluster === d) {
          return;
        }
        x = d.x - cluster.x;
        y = d.y - cluster.y;
        l = Math.sqrt(x * x + y * y);
        r = NODE_RADIUS + cluster.radius;
        if (l !== r) {
          l = (l - r) / l * alpha;
          d.x -= x *= l;
          return d.y -= y *= l;
        }
      };
    };
  }).service('collide', function() {
    return function(alpha, nodes, CLUSTER_PADDING) {
      var padding, quadtree;
      if (CLUSTER_PADDING == null) {
        CLUSTER_PADDING = 15;
      }
      padding = 2;
      quadtree = d3.geom.quadtree(nodes);
      return function(d) {
        var nx1, nx2, ny1, ny2, r;
        r = 10;
        nx1 = d.x - r;
        nx2 = d.x + r;
        ny1 = d.y - r;
        ny2 = d.y + r;
        return quadtree.visit(function(quad, x1, y1, x2, y2) {
          var l, x, y;
          if (quad.point && quad.point !== d) {
            x = d.x - quad.point.x;
            y = d.y - quad.point.y;
            l = Math.sqrt(x * x + y * y);
            r = NODE_RADIUS + quad.point.radius + (d.cluster === quad.point.cluster ? padding : CLUSTER_PADDING);
            if (l < r) {
              l = (l - r) / l * alpha;
              d.x -= x *= l;
              d.y -= y *= l;
              quad.point.x += x;
              quad.point.y += y;
            }
          }
          return x1 > nx2 || x2 < nx1 || y1 > ny2 || y2 < ny1;
        });
      };
    };
  });

}).call(this);



(function() {
  angular.module('politicalGaps').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function($log, githubContributor) {
      'ngInject';
      var activate, getContributors, vm;
      vm = this;
      activate = function() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      };
      getContributors = function() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;
          return vm.contributors;
        });
      };
      vm.contributors = [];
      activate();
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      watcher = void 0;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);



(function() {
  angular.module('politicalGaps').factory('githubContributor', function($log, $http) {
    'ngInject';
    var apiHost, getContributors, service;
    apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';
    getContributors = function(limit) {
      var getContributorsComplete, getContributorsFailed;
      if (limit == null) {
        limit = 30;
      }
      getContributorsComplete = function(response) {
        return response.data;
      };
      getContributorsFailed = function(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      };
      return $http.get(apiHost + '/contributors?per_page=' + limit).then(getContributorsComplete)["catch"](getContributorsFailed);
    };
    return service = {
      apiHost: apiHost,
      getContributors: getContributors
    };
  });

}).call(this);



