        this.getFilteredLegislatures = bind(this.getFilteredLegislatures, this);
        this.buildFilteredLegislatures = bind(this.buildFilteredLegislatures, this);
        this.isFiltered = bind(this.isFiltered, this);
        this.toggleFrom = bind(this.toggleFrom, this);
        this.toggleTerm = bind(this.toggleTerm, this);
        this.toggleDifficultyLevel = bind(this.toggleDifficultyLevel, this);
        this.toggleCountryAlpha2 = bind(this.toggleCountryAlpha2, this);
        this.matchTerm = bind(this.matchTerm, this);
        this.matchDifficultyLevel = bind(this.matchDifficultyLevel, this);
        this.matchCountryAlpha2 = bind(this.matchCountryAlpha2, this);
        this.isTerm = bind(this.isTerm, this);
        this.isDifficultyLevel = bind(this.isDifficultyLevel, this);
        this.isCountryAlpha2 = bind(this.isCountryAlpha2, this);
        this.getTerm = bind(this.getTerm, this);
        this.getDifficultyLevel = bind(this.getDifficultyLevel, this);
        this.getCountryAlpha2 = bind(this.getCountryAlpha2, this);
        this.hasTerm = bind(this.hasTerm, this);
        this.hasDifficultyLevel = bind(this.hasDifficultyLevel, this);
